import "./styles.css";

var dFreccia;
var counter = 0;

function change() {
  dFreccia = "&rarr;";
  counter++;
  var m = Math.random();
  if (m > 0.5) {
    dFreccia = "&larr;";
  }
  var codice =
    `<h1>Aeroporto X</h1> 
                <div>Si prega di andare in questa direzione</div>
                <div class='freccia'>` +
    dFreccia +
    `</div>
    <div class='contatore'>` +
    counter +
    `
    </div>
    `;

  document.getElementById("app").innerHTML = codice;
  console.log(m);
}

function main() {
  change();
  document.querySelector("body").addEventListener("click", change);
}

main();
